@import url(https://fonts.googleapis.com/css?family=Rubik);
* {
  margin: 0;
  padding: 0; }

a {
  color: #1DB954;
  font-family: 'Rubik', sans-serif;
  font-weight: normal;
  font-size: 20px; }

.button-filled {
  font-family: 'Rubik', sans-serif;
  font-weight: normal;
  font-size: 20px;
  color: white;
  background: #1DB954;
  border-radius: 10px;
  padding: 10px 35px;
  cursor: pointer;
  text-align: center;
  border: 1px solid #1DB954; }

.button-outline {
  font-family: 'Rubik', sans-serif;
  font-weight: normal;
  font-size: 20px;
  color: #1DB954;
  background: white;
  border-radius: 10px;
  padding: 10px 35px;
  cursor: pointer;
  text-align: center;
  border: 1px solid #1DB954; }

.button-disabled {
  font-family: 'Rubik', sans-serif;
  font-weight: normal;
  font-size: 20px;
  color: white;
  background: rgba(0, 0, 0, 0.5);
  border-radius: 10px;
  padding: 10px 35px;
  cursor: pointer;
  text-align: center; }

.group {
  background: white;
  width: 100%;
  box-shadow: 0 0 25px rgba(0, 0, 0, 0.5);
  border-radius: 10px; }

.nav {
  height: 80px;
  display: grid;
  grid-template-columns: 1fr 1fr;
  background: #1DB954;
  padding: 0 25px;
  box-shadow: 0 5px 25px #1DB954;
  align-items: center; }
  .nav .nav-title {
    color: white;
    font-family: 'Rubik', sans-serif;
    font-weight: bold;
    font-size: 28px;
    display: inline-block; }
  .nav .nav-right {
    display: flex;
    justify-content: flex-end; }

.plug {
  display: grid;
  grid-template-columns: 1fr 3fr;
  padding: 25px 0; }
  .plug-visual {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center; }
    .plug-visual__switch {
      margin-top: 15px; }
  .plug-info__title {
    font-family: 'Rubik', sans-serif;
    font-weight: bold;
    font-size: 28px; }
    .plug-info__title span {
      color: #1DB954; }
  .plug-info__metrics {
    margin-top: 10px;
    font-family: 'Rubik', sans-serif;
    font-weight: normal;
    font-size: 20px;
    display: grid;
    grid-row-gap: 5px; }
    .plug-info__metrics div span {
      color: #1DB954; }

.switch {
  background: white;
  border: 0.5px solid #1DB954;
  border-radius: 10px;
  display: grid;
  grid-template-columns: 1fr 1fr;
  font-family: 'Rubik', sans-serif;
  font-weight: normal;
  font-size: 20px;
  font-size: 14px;
  box-shadow: 0 0 10px #1DB954;
  color: #1DB954;
  overflow: hidden; }
  .switch__side {
    padding: 5px 10px;
    text-align: center;
    cursor: pointer;
    -webkit-transition: 200ms all;
    transition: 200ms all; }
  .switch__toggled {
    background: #1DB954;
    color: white;
    -webkit-transition: 200ms all;
    transition: 200ms all; }

.home-content {
  max-width: 1180px;
  margin: 50px auto;
  display: grid;
  grid-row-gap: 50px; }

.home-jumbotron__title {
  font-family: 'Rubik', sans-serif;
  font-weight: bold;
  font-size: 36px;
  margin-top: 25px;
  text-align: center; }
  .home-jumbotron__title span {
    color: #1DB954; }

.home-jumbotron__text {
  margin: 25px;
  font-family: 'Rubik', sans-serif;
  font-weight: normal;
  font-size: 20px; }

.home-inputs {
  display: grid;
  grid-template-columns: 200px 1fr 200px; }
  .home-inputs__left {
    border-right: 0.5px solid rgba(0, 0, 0, 0.25); }
  .home-inputs__right {
    border-left: 0.5px solid rgba(0, 0, 0, 0.25); }

.home-input {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  padding: 25px 0; }
  .home-input__data {
    padding-top: 15px;
    font-family: 'Rubik', sans-serif;
    font-weight: normal;
    font-size: 20px; }
    .home-input__data span {
      color: #1DB954; }

.home-accumulator {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column; }
  .home-accumulator__title {
    font-family: 'Rubik', sans-serif;
    font-weight: bold;
    font-size: 28px; }
  .home-accumulator__indicator {
    margin-top: 15px;
    background: white;
    width: 80%;
    height: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 10px;
    box-shadow: 0 0 25px #1DB954;
    position: relative;
    overflow: hidden; }
    .home-accumulator__indicator .data {
      font-family: 'Rubik', sans-serif;
      font-weight: normal;
      font-size: 20px;
      color: #1DB954; }
    .home-accumulator__indicator .mover {
      -webkit-transition: 200ms all;
      transition: 200ms all;
      left: 0;
      position: absolute;
      background: #1DB954;
      height: 50px; }
  .home-accumulator__prediction {
    margin-top: 15px;
    font-family: 'Rubik', sans-serif;
    font-weight: normal;
    font-size: 18px;
    font-style: italic;
    color: rgba(0, 0, 0, 0.5); }
    .home-accumulator__prediction span {
      font-weight: bold; }

.home-outputs__title {
  font-family: 'Rubik', sans-serif;
  font-weight: bold;
  font-size: 36px;
  text-align: center; }

.home-outputs__content {
  margin-top: 25px;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-column-gap: 20px;
  grid-row-gap: 20px; }

.home-add-plug {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  min-height: 180px; }
  .home-add-plug__title {
    margin-top: 15px;
    font-family: 'Rubik', sans-serif;
    font-weight: normal;
    font-size: 20px; }

.home-statistics__title {
  font-family: 'Rubik', sans-serif;
  font-weight: bold;
  font-size: 36px;
  text-align: center; }

.home-statistics__content {
  margin-top: 25px;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-row-gap: 20px;
  grid-column-gap: 20px; }

.home-graph {
  height: 350px; }

.home-support {
  display: grid;
  grid-row-gap: 10px;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  padding: 25px 0;
  text-align: center; }
  .home-support__title {
    font-family: 'Rubik', sans-serif;
    font-weight: bold;
    font-size: 36px;
    margin-bottom: 5px; }
  .home-support__text {
    font-family: 'Rubik', sans-serif;
    font-weight: normal;
    font-size: 20px; }
    .home-support__text span {
      color: #1DB954; }

